@import url("https://fonts.googleapis.com/css?family=Comfortaa|Roboto");
@import "global.css";
html {
  scroll-behavior: smooth;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Comfortaa", cursive !important;
  color: #481C51 !important;
}

h1 {
  margin: 0.67em 0 !important;
}

body,
span,
p {
  font-family: "Roboto", sans-serif;
  color: black;
}

img {
  border-radius: 2%;
}

button.btn.btn-primary {
  background-color: #481C51;
  font-family: "Comfortaa", cursive;
  color: white;
  border-radius: 25px 25px;
  outline: none;
  border: none;
  cursor: pointer;
  text-decoration: none;
  padding: 0.5em 2em;
  transition: all 0.5s;
}
button.btn.btn-primary:hover {
  background-color: #E1AE2C;
}
button.btn.btn-primary:focus, button.btn.btn-primary:active {
  box-shadow: none;
}

.navbar {
  padding: 1.2em 4em !important;
  background-color: white;
  transition: all ease-in 0.3s;
}
.navbar.active {
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
}
.navbar .navbar-brand * {
  width: 5em;
}
.navbar li {
  cursor: pointer;
}
.navbar li a {
  font-family: "Comfortaa", cursive;
  color: #481C51 !important;
  text-transform: lowercase;
  font-size: 1em;
  margin-left: 1.4em;
  transition: all ease-out 0.2s;
  font-weight: bold;
}
.navbar li a:hover {
  color: #E1AE2C !important;
}
.navbar li a.disabled {
  color: grey !important;
  cursor: default;
}
.navbar li.contact a {
  background-color: #481C51;
  color: white !important;
  padding: 0.5em 2em !important;
  border-radius: 25px 25px;
  margin-left: 2.5em !important;
}
.navbar li.contact a:hover {
  background-color: #E1AE2C;
}

button.navbar-toggler {
  font-family: "Comfortaa", cursive;
  text-transform: lowercase;
  font-size: 1em;
  background-color: #481C51;
  color: white !important;
  padding: 0.5em 1.5em !important;
  border-radius: 25px 25px;
  outline: none;
  transition: all 0.5s;
}
button.navbar-toggler:hover {
  background-color: #E1AE2C;
}

:target {
  display: block;
  position: relative;
  top: -120px;
  visibility: hidden;
}

.section h1 {
  font-size: 3em;
  text-transform: lowercase;
  text-align: center;
}

footer {
  margin: 5em 0 3em 0;
}
footer h6 {
  font-weight: bold;
  text-transform: lowercase;
  margin-bottom: 1.5em;
}
footer i {
  color: #E1AE2C;
  width: 1em;
  font-size: 1.1em;
}
footer p {
  margin: 0;
  padding-left: 1em;
  font-size: 0.9em;
}

@media (max-width: 991.98px) {
  .navbar {
    padding: 1.2em 2em !important;
  }
  .navbar li a {
    text-align: center;
    margin-left: 0;
    color: #481C51 !important;
  }
  .navbar li:first-child {
    margin-top: 1.5em;
  }
  .navbar li.contact a {
    background-color: transparent;
    color: #481C51 !important;
    padding: default;
    border-radius: 0;
    margin-left: 0 !important;
  }
  .navbar li.contact a:hover {
    background-color: transparent;
    color: #E1AE2C !important;
  }
}
#banner {
  height: 100vh;
}
#banner h1 {
  font-size: 3em;
}
#banner p {
  color: #E1AE2C;
  padding-right: 8em;
  font-size: 1.2em;
}

#about .description {
  padding: 0 7.8em;
  text-align: center;
}
#about #about-cards {
  margin: 6em 0 8em 0;
  position: relative;
}
#about #about-cards .card {
  padding: 1.5em;
  border-radius: 25px 25px;
  max-width: 20rem;
  height: 100%;
  margin: auto;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  border: none;
  transition: all ease-out 0.3s;
}
#about #about-cards .card img {
  height: 10em;
}
#about #about-cards .card .card-title {
  font-size: 1.4em;
}
#about #about-cards .card .card-body {
  padding-top: 2.5em;
}
#about #about-cards .card:hover {
  transform: scale(1.03);
}
#about #about-cards:before {
  content: "";
  display: inline-block;
  width: 100vw;
  height: 50vw;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  margin: auto;
  background-image: url("../svgRoxoDesktop.svg");
  top: 0;
  bottom: 0;
  left: calc((1140px - 100vw)/2 - 15px);
}
@media (max-width: 1200px) {
  #about #about-cards:before {
    left: calc((960px - 100vw)/2 - 15px);
  }
}
@media (max-width: 992px) {
  #about #about-cards:before {
    left: calc((720px - 100vw)/2 - 15px);
    background-image: url("../svgRoxoMobile.svg");
    height: 1500px;
  }
}
@media (max-width: 768px) {
  #about #about-cards:before {
    left: calc((540px - 100vw)/2 - 15px);
  }
}
@media (max-width: 576px) {
  #about #about-cards:before {
    left: -15px;
  }
}

#projects .row {
  margin: 5em 0 8em 0;
  position: relative;
  min-height: 500px;
}
#projects .row > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (max-width: 992px) {
  #projects .row img {
    max-height: 300px;
  }
}
#projects button {
  margin-top: 2em;
  width: fit-content;
}
#projects .row:before {
  content: "";
  display: inline-block;
  width: 50vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
}
#projects .row:nth-child(even):before {
  right: 50%;
}
@media (max-width: 992px) {
  #projects .row:nth-child(even):before {
    left: calc((720px - 100vw)/2 - 15px);
    right: auto;
  }
}
@media (max-width: 768px) {
  #projects .row:nth-child(even):before {
    left: calc((540px - 100vw)/2 - 15px);
  }
}
@media (max-width: 576px) {
  #projects .row:nth-child(even):before {
    left: -15px;
    width: 70vw;
    bottom: auto;
  }
}
#projects .row:nth-child(odd):before {
  left: 50%;
  background-position: right;
}
@media (max-width: 992px) {
  #projects .row:nth-child(odd):before {
    right: calc((720px - 100vw)/2 - 15px);
    left: auto;
    background-position: right top;
  }
}
@media (max-width: 768px) {
  #projects .row:nth-child(odd):before {
    right: calc((540px - 100vw)/2 - 15px);
  }
}
@media (max-width: 576px) {
  #projects .row:nth-child(odd):before {
    right: -15px;
    width: 70vw;
    bottom: auto;
    top: -100px;
  }
}
#projects .row:nth-child(2):before {
  background-image: url("../svgLaranjaDesktop.svg");
  max-height: 600px;
}
@media (max-width: 576px) {
  #projects .row:nth-child(2):before {
    top: -30px;
  }
}
#projects .row:nth-child(3):before {
  background-image: url("../svgVerdeDesktop.svg");
  max-height: 800px;
}
#projects .row:nth-child(4):before {
  background-image: url("../svgAzulDesktop.svg");
  max-height: 800px;
}
@media (max-width: 576px) {
  #projects .row:nth-child(4):before {
    top: -150px;
  }
}
#projects .row:nth-child(5):before {
  background-image: url("../svgRosaDesktop.svg");
  max-height: 800px;
}

@media (max-width: 991.98px) {
  #banner {
    padding-top: 10em;
    height: auto;
  }
  #banner p {
    padding: 0 0 2em 0;
  }

  #about .description {
    padding: 0;
  }
  #about div[class^=col-]:not(:first-child) {
    margin-top: 4em;
  }

  #projects div[class=row]:nth-child(2n+1) div[class^=col-]:first-child {
    margin-top: 4em;
  }
  #projects div[class^=row]:nth-child(2n+2) div[class^=col-]:not(:first-child) {
    margin-top: 4em;
  }
}
/* Table of Contents
/* ------------------------------------------------------------

This is a development CSS file which is built to a minified
production stylesheet in assets/built/screen.css

1.  Global Styles
2.  Layout
3.  Special Templates
4.  Site Header
5.  Site Navigation
6.  Post Feed
7.  Single Post
  7.1. Subscribe Form
  7.2. Post Footer
     7.2.1 Single Author Byline
     7.2.2 Multiple Author Byline
  7.3. Comments
  7.4. Related Posts
  7.5. Floating Header
  7.6. Koenig Styles
8.  Author Template
9.  Error Template
10. Subscribe Overlay
11. Site Footer

*/
/* 1. Global - Set up the things
/* ---------------------------------------------------------- */
body {
  background: #f4f8fb;
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  background-position: center center;
  background-size: cover;
  border-radius: 100%;
}

.hidden {
  visibility: hidden;
  position: absolute;
  text-indent: -9999px;
}

/* 2. Layout - Page building blocks
/* ---------------------------------------------------------- */
.site-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.site-main {
  z-index: 100;
  flex-grow: 1;
}

/* Full width page blocks */
.outer {
  position: relative;
  padding: 0 4vw;
}

/* Centered content container blocks */
.inner {
  margin: 0 auto;
  max-width: 1040px;
  width: 100%;
}

/* Usage:

<div class="outer">
    <div class="inner">
        Centered content
    </div>
</div>

*/
/* 3. Special Template Styles
/* ---------------------------------------------------------- */
@media (min-width: 900px) {
  .home-template .post-feed,
.tag-template .post-feed,
.author-template .post-feed {
    margin-top: -70px;
    padding-top: 0;
  }

  .home-template .site-nav {
    position: relative;
    top: -70px;
  }
}
/* 5. Site Navigation
/* ---------------------------------------------------------- */
.site-nav {
  position: relative;
  z-index: 300;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  overflow-y: hidden;
  height: 40px;
  font-size: 1.2rem;
}

.site-nav-left {
  display: flex;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
  margin-right: 10px;
  padding-bottom: 80px;
  letter-spacing: 0.4px;
  white-space: nowrap;
  -ms-overflow-scrolling: touch;
}

/* Site Nav Hack Explanation (above):

What's happening above it .site-nav-left is set to overflow-x and allow sideways scrolling, so that when there isn't enough space for all nav items (either due to lots of nav items, or a small viewport), you can still scroll side-to-side to reach them.

The knock-on effect of this is ugly browser-scroll bars at the bottom, so 80px of padding-bottom and a 40px fixed height parent (.site-nav) hides that entirely. Slightly hacky code. But nice clean end-result.

*/
.site-nav-logo {
  flex-shrink: 0;
  display: block;
  margin-right: 24px;
  padding: 11px 0;
  color: #fff;
  font-size: 1.7rem;
  line-height: 1em;
  font-weight: bold;
  letter-spacing: -0.5px;
}

.site-nav-logo:hover {
  text-decoration: none;
}

.site-nav-logo img {
  display: block;
  width: auto;
  height: 21px;
}

.nav {
  display: flex;
  margin: 0 0 0 -12px;
  padding: 0;
  list-style: none;
}

.nav li {
  display: block;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.nav li a {
  display: block;
  margin: 0;
  padding: 10px 12px;
  color: #fff;
  opacity: 0.8;
}

.nav li a:hover {
  text-decoration: none;
  opacity: 1;
}

.site-nav-right {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  height: 40px;
}

.social-links {
  flex-shrink: 0;
  display: flex;
  align-items: center;
}

.social-links a:last-of-type {
  padding-right: 20px;
}

.social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 10px;
  color: #fff;
  opacity: 0.8;
}

.social-link:hover {
  opacity: 1;
}

.social-link svg {
  height: 1.8rem;
  fill: #fff;
}

.social-link-fb svg {
  height: 1.5rem;
}

.social-link-wb svg {
  height: 1.6rem;
}

.social-link-wb svg path {
  stroke: #fff;
}

.social-link-rss svg {
  height: 1.9rem;
}

.subscribe-button {
  display: block;
  padding: 4px 10px;
  border: #fff 1px solid;
  color: #fff;
  font-size: 1.2rem;
  line-height: 1em;
  border-radius: 10px;
  opacity: 0.8;
}

.subscribe-button:hover {
  text-decoration: none;
  opacity: 1;
}

.rss-button {
  opacity: 0.8;
}

.rss-button:hover {
  opacity: 1;
}

.rss-button svg {
  margin-bottom: 1px;
  height: 2.1rem;
  fill: #fff;
}

@media (max-width: 700px) {
  .site-header {
    padding-right: 0;
    padding-left: 0;
  }

  .site-nav-left {
    margin-right: 0;
    padding-left: 4vw;
  }

  .site-nav-right {
    display: none;
  }
}
/* 6. Post Feed
/* ---------------------------------------------------------- */
.post-feed {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -20px;
  padding: 40px 0 0 0;
}

.post-card {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin: 0 20px 40px;
  min-height: 300px;
  background: #fff center center;
  background-size: cover;
  border-radius: 5px;
  box-shadow: rgba(39, 44, 49, 0.06) 8px 14px 38px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
  transition: all 0.5s ease;
}

.post-card:hover {
  box-shadow: rgba(39, 44, 49, 0.07) 8px 28px 50px, rgba(39, 44, 49, 0.04) 1px 6px 12px;
  transition: all 0.4s ease;
  transform: translate3D(0, -1px, 0) scale(1.02);
}

.post-card-image-link {
  position: relative;
  display: block;
  overflow: hidden;
  border-radius: 5px 5px 0 0;
}

.post-card-image {
  width: 100%;
  height: 200px;
  background: var(--lightgrey) no-repeat center center;
  object-fit: cover;
}

.post-card-content-link {
  position: relative;
  flex-grow: 1;
  display: block;
  padding: 25px 25px 0;
  color: var(--darkgrey);
}

.post-card-content-link:hover {
  text-decoration: none;
}

.post-card-tags {
  display: block;
  margin-bottom: 4px;
  color: var(--midgrey);
  font-size: 1.2rem;
  line-height: 1.15em;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

.post-card-title {
  margin-top: 0;
}

.post-card-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.post-card-excerpt {
  font-family: Georgia, serif;
}

.post-card-meta {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0 25px 25px;
}

.author-profile-image,
.avatar-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  background: color(var(--lightgrey) l(10%));
  border-radius: 100%;
  object-fit: cover;
}

.post-card-meta .profile-image-wrapper,
.post-card-meta .avatar-wrapper {
  position: relative;
}

.author-list {
  display: flex;
  flex-wrap: wrap-reverse;
  margin: 0;
  padding: 0;
  list-style: none;
}

.author-list-item {
  position: relative;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
}

.author-list-item:nth-child(1) {
  z-index: 10;
}

.author-list-item:nth-child(2) {
  z-index: 9;
}

.author-list-item:nth-child(3) {
  z-index: 8;
}

.author-list-item:nth-child(4) {
  z-index: 7;
}

.author-list-item:nth-child(5) {
  z-index: 6;
}

.author-list-item:nth-child(6) {
  z-index: 5;
}

.author-list-item:nth-child(7) {
  z-index: 4;
}

.author-list-item:nth-child(8) {
  z-index: 3;
}

.author-list-item:nth-child(9) {
  z-index: 2;
}

.author-list-item:nth-child(10) {
  z-index: 1;
}

.static-avatar {
  display: block;
  overflow: hidden;
  margin: 0 -5px;
  width: 34px;
  height: 34px;
  border: #fff 2px solid;
  border-radius: 100%;
}

.moving-avatar {
  display: block;
  overflow: hidden;
  margin: 0 -6px;
  width: 56px;
  height: 56px;
  border: #fff 2px solid;
  border-radius: 100%;
  transition: all 0.5s cubic-bezier(0.4, 0.01, 0.165, 0.99) 0.7s;
}

@media (min-width: 800px) {
  .author-list:hover .moving-avatar {
    margin: 0;
    transition: all 0.3s cubic-bezier(0.4, 0.01, 0.165, 0.99);
  }
}
.author-name-tooltip {
  position: absolute;
  bottom: 105%;
  z-index: 999;
  display: block;
  padding: 2px 8px;
  color: white;
  font-size: 1.2rem;
  letter-spacing: 0.2px;
  white-space: nowrap;
  background: var(--darkgrey);
  border-radius: 3px;
  box-shadow: rgba(39, 44, 49, 0.08) 0 12px 26px, rgba(39, 44, 49, 0.03) 1px 3px 8px;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.4, 0.01, 0.165, 0.99);
  transform: translateY(6px);
  pointer-events: none;
}

.author-list-item:hover .author-name-tooltip {
  opacity: 1;
  transform: translateY(0px);
}

@media (max-width: 650px) {
  .author-name-tooltip {
    display: none;
  }
}
.reading-time {
  flex-shrink: 0;
  margin-left: 20px;
  color: var(--midgrey);
  font-size: 1.2rem;
  line-height: 33px;
  font-weight: 500;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* Special Styling for home page grid (below):

The first post in the list is styled to be bigger than the others and take over
the full width of the grid to give it more emphasis. Wrapped in a media query to
make sure this only happens on large viewports / desktop-ish devices.

 */
@media (min-width: 795px) {
  .post-card-large {
    flex: 1 1 100%;
    flex-direction: row;
  }

  .post-card-large .post-card-image-link {
    position: relative;
    flex: 1 1 auto;
    border-radius: 5px 0 0 5px;
  }

  .post-card-large .post-card-image {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .post-card-large .post-card-content {
    flex: 0 1 357px;
  }

  .post-card-large h2 {
    font-size: 2.6rem;
  }

  .post-card-large p {
    font-size: 1.8rem;
    line-height: 1.55em;
  }

  .post-card-large .post-card-content-link {
    padding: 30px 40px 0;
  }

  .post-card-large .post-card-meta {
    padding: 0 40px 30px;
  }
}
.home-template .site-header:after {
  display: none;
}

/* Adjust some margins for smaller screens */
@media (max-width: 650px) {
  .post-feed {
    padding-top: 5vw;
  }

  .post-card {
    margin: 0 20px 5vw;
  }
}
/* 7. Single Post
/* ---------------------------------------------------------- */
.post-template .site-main,
.page-template .site-main {
  padding-bottom: 4vw;
  background: #fff;
}

.post-full {
  position: relative;
  z-index: 50;
}

/* ^ Required to make .post-full-content:before/after z-index stacking work */
.post-full-header {
  margin: 0 auto;
  padding: 6vw 3vw 3vw;
  max-width: 1040px;
  text-align: center;
}

@media (max-width: 500px) {
  .post-full-header {
    padding: 14vw 3vw 10vw;
  }
}
.post-full-meta {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--midgrey);
  font-size: 1.4rem;
  font-weight: 600;
  text-transform: uppercase;
}

.post-full-meta-date {
  color: var(--blue);
}

.post-full-title {
  margin: 0;
  color: color(var(--darkgrey) l(-5%));
}

.date-divider {
  display: inline-block;
  margin: 0 6px 1px;
}

.post-full-image {
  margin: 0 -10vw -165px;
  background: var(--lightgrey) center center;
  border-radius: 5px;
  overflow: hidden;
}

.post-full-image img {
  width: 100%;
  height: 800px;
  object-fit: cover;
}

@media (max-width: 1170px) {
  .post-full-image {
    margin: 0 -4vw -100px;
    border-radius: 0;
  }

  .post-full-image img {
    height: 600px;
  }
}
@media (max-width: 800px) {
  .post-full-image img {
    height: 400px;
  }
}
.post-full-content {
  position: relative;
  margin: 0 auto;
  padding: 70px 100px 0;
  min-height: 230px;
  font-family: Georgia, serif;
  font-size: 2.2rem;
  line-height: 1.6em;
  background: #fff;
}

@media (max-width: 1170px) {
  .post-full-content {
    padding: 5vw 7vw 0;
  }
}
