/* 7.5. Floating Header
/* ---------------------------------------------------------- */
.floating-header {
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  height: 60px;
  border-bottom: rgba(0, 0, 0, 0.06) 1px solid;
